import { AppService } from './services/app.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
    title = 'quiz';
    quizName = '';
    data: any = {};

    currentTask: any = {};
    resStr = '';
    answer = '';
    taskId = '';

    isSuccess = false;

    successInfo: any = {};

    constructor(private appSrv: AppService, private router: Router) {}

    ngOnInit() {
        // this.appSrv.setStorageData('2');
        this.data = this.appSrv.getData();
        this.quizName = this.data.settings.name;
        this.appSrv.setNextTask();

        // this.appSrv.getJSON().subscribe(data => {
        //   this.data = data;
        //   this.appSrv.setData(data);
        //   this.appSrv.setNextTask();
        //   this.quizName = this.data.settings.name;
        // });

        this.appSrv.setSuccessResultEvt.subscribe((obj: any) => {
            //console.log("success");
            //console.log(obj);

            if (obj.info) {
                this.successInfo = this.appSrv.getSuccessInfo();
                this.isSuccess = true;
                // this.router.navigate(["/success"]);
            }
        });

        this.appSrv.updCurrentTaskEvt.subscribe(task => {
            this.currentTask = task;
        });
    }

    onSubmit() {
        if (this.answer) {
            this.resStr = `Ваш ответ [${this.answer}] неверный.`;

            if (this.checkAnswer()) {
                this.resStr = `Ваш ответ [${this.answer}] верный!`;
                this.answer = '';
                this.appSrv.setNextTask(true);
            }
        }
    }

    checkAnswer() {
        if (typeof this.currentTask.answer === 'object') {
            let ind = this.currentTask.answer.findIndex((element: any) => {
                return element.toLowerCase() === this.answer.toLowerCase();
            });

            if (ind >= 0) return true;
        } else if (
            this.answer.toLowerCase() === this.currentTask.answer.toLowerCase()
        ) {
            return true;
        }

        return false;
    }

    onTest() {
        console.log('Loacl data: ', this.appSrv.getStorageData());
    }

    onTest2() {
      this.appSrv.setStorageData('2');
    }

    onTest3() {
      window.location.reload();
    }

    onNewGame() {
        this.appSrv.setStorageData('');
        // this.appSrv.setNextTask();
        window.location.reload();
    }
}
